import { Injectable } from '@angular/core';
import { RequestStatus } from '@app/modules/newscenter/RequestStatus.model';
import { ClubModel, MyClappService } from '@myclapp/myclapp-server';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import {Logger} from '@upe/logger'
import {BaseConfigService} from './base.config.service';


import { UserService } from './user.service';

@Injectable({ providedIn: 'root' })
export class ClubModelService extends BaseConfigService<ClubModel>{
    protected _cachedConfig: ClubModel|null = null;
    constructor(private apiClient: MyClappService, private userService: UserService) {
        super(ClubModelService.name);
    }

    protected _getCachedConfig(): ClubModel|null{
        return this._cachedConfig
    }

    submit(data: ClubModel): Observable<ClubModel> {
        return this.apiClient.updateClub({body: data, clubId: this.userService.club_id})
    }

    get(refresh?: true): Observable<ClubModel>{
        if (this._getCachedConfig() == null || refresh) return this._refresh()
        // @ts-ignore
        return of(this._getCachedConfig())
    }

    protected _refresh(): Observable<ClubModel> {
        return this.apiClient.getClub({clubId:this.userService.club_id})
    }
}
