import { Injectable } from '@angular/core';
import {ClubConfigModel, MyClappService} from '@myclapp/myclapp-server';
import { Observable, of } from 'rxjs';
import {BaseConfigService} from './base.config.service';


import { UserService } from './user.service';


@Injectable({ providedIn: 'root' })
export class NewscenterService  extends BaseConfigService<ClubConfigModel>{

    constructor(private apiClient:MyClappService, private userService:UserService) {
        super(NewscenterService.name);
    }

    protected _cachedConfig: ClubConfigModel|null = null;

    protected _getCachedConfig(): ClubConfigModel| null{
        return  this._cachedConfig
    }

    protected _refresh(): Observable<ClubConfigModel> {
        return this.apiClient.getConfig({clubId: this.userService.club_id})
    }

    get(refresh?: true): Observable<ClubConfigModel> {
        if (refresh || this._getCachedConfig() == null ) return this._refresh()
        // @ts-ignore bc we catch null above
        return of(this._getCachedConfig())
    }

    submit(data: ClubConfigModel): Observable<ClubConfigModel> {
        return this.apiClient.updateConfig({clubId: this.userService.club_id, body: data})
    }
}
