import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
    {
        path: '',
        pathMatch: 'full',
        redirectTo: '/dashboard',
    },
    {
        path: 'dashboard',
        loadChildren: () =>
            import('app/modules/dashboard/dashboard-routing.module').then(
                m => m.DashboardRoutingModule,
            ),
    },
    {
        path: 'account',
        loadChildren: () =>
            import('app/modules/auth/auth-routing.module').then(m => m.AuthRoutingModule),
    },
    {
        path: 'error',
        loadChildren: () =>
            import('app/modules/error/error-routing.module').then(m => m.ErrorRoutingModule),
    },
    {
        path: 'newscenter',
        loadChildren: () =>
            import('app/modules/newscenter/newscenter-routing.module').then(m => m.NewscenterRoutingModule),
    },
    {
        path: 'clubmodel',
        loadChildren: () =>
            import('@app/modules/clubmodel/club-model-routing.module').then(m => m.ClubModelRoutingModule),
    },
    {
        path: 'products',
        loadChildren: () =>
            import('@app/modules/products/products-routing.module').then(m => m.ProductsRoutingModule),
    },
    {
        path: 'stadium',
        loadChildren: () =>
            import('@app/modules/corona-distribution/corona-distribution-routing.module').then(m => m.CoronaDistributionRoutingModule),
    },
    {
        path: 'push',
        loadChildren: () =>
            import('@app/modules/push/push-routing.module').then(m => m.PushRoutingModule),
    },
    {
        path: 'intro',
        loadChildren: () =>
            import('@app/modules/intro/intro-routing.module').then(m => m.IntroRoutingModule),
    },
    {
        path: 'sponsors',
        loadChildren: () =>
            import('@app/modules/sponsoring/sponsoring-routing.module').then(m => m.SponsoringRoutingModule),
    },
    {
        path: '**',
        pathMatch: 'full',
        loadChildren: () =>
            import('app/modules/error/error-routing.module').then(m => m.ErrorRoutingModule),
    },
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule],
})
export class AppRoutingModule {
}
