import {Injectable} from '@angular/core';
import {MyClappService} from '@myclapp/myclapp-server';
import {Observable} from 'rxjs';
import {UserService} from './user.service';
import {Logger} from '@upe/logger'


@Injectable({providedIn: 'root'})
export abstract class BaseConfigService<T> {

    protected _logger: Logger;

    protected abstract _cachedConfig: T|T[]|null;

    constructor(name:string) {
        this._logger = new Logger({ name: name, flags: ["service"] });
    }

    abstract submit(data:T):Observable<T>|Observable<T[]>
    protected abstract _refresh(): Observable<T|T[]>
    protected abstract _getCachedConfig():T|T[]|null
    abstract get(refresh?:true): Observable<T|T[]>


}
